.modal{
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: scale(0);
}

.modal.active{
  transform: scale(1);
}
.modal_content{
  height: 80vh;
  width: auto;
  padding: 20px;
  border-radius: 12px;
  background-color: aliceblue;
}